import {
  I_Hotspot,
  I_HotspotConfigStyle,
  I_Scene,
} from '@containers/Home/types';
import dynamic from 'next/dynamic';
import React, { memo, useMemo } from 'react';
import { MarkerIconWrapper } from './styled';

const SvgContainer = dynamic(() => import('@components/SvgContainer'));

interface Props {
  hotspot: I_Hotspot;
  marker?: I_HotspotConfigStyle['marker'];
  listScene: I_Scene[];
}

const MarkerIcon = ({ hotspot, marker, listScene }: Props) => {
  const targetSceneHotspotPoint = useMemo(
    () =>
      hotspot.config?.point?.target_scene_id
        ? listScene.filter(
            item => item.id === hotspot?.config?.point?.target_scene_id,
          )[0] || undefined
        : undefined,
    [hotspot, listScene],
  );

  const targetSceneHotspotPointImgSrc = useMemo(() => {
    if (hotspot.type === 'point') {
      if (hotspot.config?.point?.preview_box_img_type === 'thumbnail')
        return (
          targetSceneHotspotPoint?.screenshot ||
          targetSceneHotspotPoint?.media?.src_thumb
        );
      if (
        hotspot.config?.point?.preview_box_img_type === 'upload' &&
        hotspot.config?.point?.preview_box_img_src
      )
        return hotspot?.config?.point?.preview_box_img_src;
      return targetSceneHotspotPoint?.media?.src_thumb;
    }
    return '';
  }, [targetSceneHotspotPoint, hotspot]);

  return (
    <MarkerIconWrapper color={marker?.color || ''}>
      <SvgContainer src={marker?.svg_src || ''} name={marker?.svg_name || ''}>
        <img src={targetSceneHotspotPointImgSrc || ''} alt="" />
      </SvgContainer>
    </MarkerIconWrapper>
  );
};

export default memo(MarkerIcon);
